@import url(https://fonts.googleapis.com/css?family=Oxygen);
body {
  margin: 0;
  font-family: "Oxygen", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #f9f7f3 !important;
  background-color: #030303 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  list-style: disc;
  margin: 0;
  padding: 0;
}

li a {
  color: #e0e0e2;
  text-decoration: none;
  font-weight: 500;
  font-size: larger;
  transition: all 0.2s ease-in-out;
}

li a:hover {
  color: #f97068 !important;
}

.nav-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.header {
  display: flex;
  padding: 15px;
  height: 10vh;
  align-items: center;
}

.left,
.right {
  flex: 1 1;
}

.branding {
  display: flex;
  align-items: center;
}

ul {
  margin-bottom: 0 !important;
}

.nav > ul > li {
  display: inline-block;
  padding: 5px 0px;
  margin: 0px 5px !important;
}

.branding img {
  max-height: 50px;
  width: auto;
}

@media only screen and (max-width: 1023px) {
  .branding img {
    max-height: 25px;
    width: auto;
  }
  .nav {
    font-size: x-small;
  }
}

@media only screen and (max-width: 600px) {
  .nav {
    font-size: 50%;
  }

  .nav > ul > li {
    margin: 0px 2px !important;
  }

  .right {
    flex: 0 1;
  }
}

body,
html {
  /* background: #dbe2ef !important; */
  /* background-image: url("./assets/images/leaf fade.svg") !important; */
  height: 100%;
}

h1,
h3 {
  text-align: center;
}

.jumbotron {
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-image: url("./assets/images/leaf.svg") !important; */
}

.name {
  font-size: 8vw;
  position: relative;
}

.name span {
  color: transparent;
  -webkit-text-stroke: 2px #4db8ff;
}

.subtitle {
  font-size: 1.5vw;
}

.subtitle span {
  color: #f97068;
}

.down-arrow {
  position: absolute;
  bottom: 2rem;
  right: 50%;
  -webkit-animation: bobble 1s infinite alternate;
          animation: bobble 1s infinite alternate;
  cursor: pointer;
  font-size: 24px;
}

@-webkit-keyframes bobble {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
  }
}

@keyframes bobble {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
  }
}

@media only screen and (max-width: 688px) {
  .name {
    font-size: 10vw;
  }
  .subtitle {
    font-size: 4vw;
    word-wrap: break-word;
  }
}

ul {
  list-style-type: none;
  list-style-position: inside;
  padding-left: 0 !important;
}

.aboutme {
  font-size: 4vw;
  line-height: 1.1em;
  position: relative;
  display: block;
  text-align: center;
  margin: 15px;
  width: 92%;
  margin: auto;
  max-width: 120rem;
}

.aboutme:after {
  content: "";
  position: absolute;
  top: calc(100% + 0.5rem);
  height: 5px;
  width: 20vw;
  background: #f97068;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border-radius: 5px;
}

#aboutme {
  height: 100vh;
  padding: 50px 50px 0px 50px;
}

.flex {
  margin-top: 15px;
  display: flex;
}

.left-col,
.right-col {
  flex: 50% 1;
  text-align: center;
  justify-content: center;
  margin: 0px 15px;
}

.headshot,
.school {
  max-width: 300px;
  height: auto;
  margin: 15px 0px;
  border-radius: 10px;
}

.skills > li {
  padding: 5px 15px;
  border-radius: 10px;
  background-color: #e0e0e2;
  color: #030303;
  margin: 5px;
  display: inline-block;
  font-size: large;
}

.text-block {
  background-color: #f97068;
  border-radius: 10px;
  padding: 15px;
}

.text {
  text-align: left;
  color: #030303;
}

@media only screen and (max-width: 992px) {
  .flex {
    display: block;
  }

  #aboutme {
    height: auto;
  }

  .headshot,
  .school {
    max-width: 90%;
  }
}

@media only screen and (max-width: 688px) {
  .aboutme {
    font-size: 8vw;
  }
}

.projects {
  font-size: 4vw;
  line-height: 1.1em;
  position: relative;
  display: block;
  text-align: center;
  margin: 15px;
}

.projects:after {
  content: "";
  position: absolute;
  top: calc(100% + 0.5rem);
  height: 5px;
  width: 15vw;
  background: #f97068;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border-radius: 5px;
}

.projects-block {
  width: 92%;
  margin: auto;
  max-width: 120rem;
}

.project {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 5rem;
}

.project-img {
  text-align: center;
  margin: auto;
}

.image {
  max-width: 450px;
  height: auto;
  margin: 15px 0px;
  border-radius: 10px;
}

.project-content {
  margin-right: 2rem;
  padding: 2rem 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
}

.project-content h2 {
  font-weight: bold;
}

.link {
  padding: 5px 30px;
  border-radius: 10px;
  background-color: transparent;
  border: 2px solid #e0e0e2;
  color: #e0e0e2;
  margin: 5px;
  display: inline-block;
  font-size: large;
  text-decoration: none;
}

.link:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #e0e0e2;
  border: 2px solid #e0e0e2;
  color: #030303;
}

.project-content p {
  line-height: 1.7;
  margin-bottom: 1rem;
  word-wrap: break-word;
}

.tags {
  display: inline-flex;
}

.software-tag {
  background-color: #f97068;
  color: #030303;
  border-radius: 10px;
  padding: 5px 15px;
  margin-right: 15px;
}

.hardware-tag {
  background-color: #4db8ff;
  color: #030303;
  border-radius: 10px;
  padding: 5px 15px;
  margin-right: 15px;
}

.tech > li {
  padding: 5px 15px;
  border-radius: 10px;
  background-color: #e0e0e2;
  color: #030303;
  margin: 5px;
  display: inline-block;
  font-size: large;
}

@media only screen and (max-width: 688px) {
  .projects {
    font-size: 8vw;
  }

  .project {
    display: inline-block;
    text-align: center;
    word-wrap: break-word;
  }

  .project-content {
    display: inline-block;
  }

  .tag {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
  }
}

@media only screen and (max-width: 767px) {
  .image {
    max-width: 300px;
  }
}

#work {
  width: 92%;
  margin: auto;
  max-width: 120rem;
}

.title span {
  font-size: 18px;
  font-weight: bold;
}

.info span {
  padding: 0px 5px 0px 2px;
}

span a {
  color: #212529;
  text-decoration: none;
}

.accordion {
  --bs-accordion-border-color: none !important;
  --bs-accordion-btn-focus-border-color: none !important;
  --bs-accordion-btn-focus-box-shadow: none !important;
  --bs-accordion-btn-active-icon: url(data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 16 16%27 fill=%27%23212529%27%3e%3cpath fill-rule=%27evenodd%27 d=%27M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z%27/%3e%3c/svg%3e) !important;
  --bs-accordion-active-color: none !important;
  --bs-accordion-bg: none !important;
  overflow: hidden;
}

.accordion-button,
.accordion-collapse {
  margin: 15px 0px;
  background-color: #e0e0e2 !important;
  border-radius: 10px !important;
  overflow: hidden;
}

.accordion-button.collapsed {
  background-color: #4db8ff !important;
}

.title {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 15px 5px 15px 0px;
}

.info {
  justify-content: start;
}

.desc {
  padding: 15px 0px;
  display: flex;
}

.expanded {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

ul {
  list-style-type: none;
  list-style-position: inside;
  padding-left: 0 !important;
}

.technologies > li {
  padding: 5px 15px;
  border-radius: 10px;
  background-color: #4db8ff;
  margin: 5px;
  display: inline-block;
}

.logo {
  display: block;
  margin: auto;
  margin-left: 15px;
  max-height: 150px;
}

svg {
  pointer-events: none;
}

.prof {
  font-size: 4vw;
  line-height: 1.1em;
  position: relative;
  display: block;
  text-align: center;
  margin: 15px;
}

.prof:after {
  content: "";
  position: absolute;
  top: calc(100% + 0.5rem);
  height: 5px;
  width: 45vw;
  background: #4db8ff;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border-radius: 5px;
}

@media only screen and (max-width: 688px) {
  .time,
  .logo {
    display: none;
  }

  .prof {
    font-size: 8vw;
  }
}

